export default "<template>\n  <div>\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Basic Select\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect\n          multiple\n          :options=\"options\"\n        >\n          <option value=\"1\">\n            一月\n          </option>\n          <option value=\"2\">\n            二月\n          </option>\n          <option value=\"3\">\n            三月\n          </option>\n          <option value=\"4\">\n            四月\n          </option>\n          <option value=\"5\">\n            五月\n          </option>\n          <option value=\"6\">\n            六月\n          </option>\n          <option value=\"7\">\n            七月\n          </option>\n          <option value=\"8\">\n            八月\n          </option>\n          <option value=\"9\">\n            九月\n          </option>\n          <option value=\"10\">\n            十月\n          </option>\n          <option value=\"11\">\n            十一月\n          </option>\n          <option value=\"12\">\n            十二月\n          </option>\n        </MultipleSelect>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      options: {\n        filter: true,\n        formatSelectAll () {\n          return '[全选]'\n        },\n        formatAllSelected () {\n          return '已选择所有记录'\n        },\n        formatCountSelected (count, total) {\n          return '已从 ' + total + ' 中选择 ' + count + ' 条记录'\n        },\n        formatNoMatchesFound () {\n          return '没有找到记录'\n        }\n      }\n    }\n  }\n}\n</script>\n\n<style scoped>\nselect {\n  width: 100%;\n}\n</style>\n"