export default "<template>\n  <div>\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Number v-model\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect\n          v-model=\"model1\"\n          :data=\"data\"\n          multiple\n          @change=\"change1\"\n        />\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Object v-model\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect\n          v-model=\"model2\"\n          @change=\"change2\"\n        >\n          <option\n            v-for=\"(row, i) in data\"\n            :key=\"i\"\n            :value=\"row\"\n          >\n            {{ row.text }}\n          </option>\n        </MultipleSelect>\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Object v-model\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect\n          v-model=\"model3\"\n          :data=\"objectData\"\n          @change=\"change3\"\n        />\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Group v-model\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect\n          v-model=\"group\"\n          :data=\"groupData\"\n          multiple\n        />\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    const data = [\n      {\n        text: 'January',\n        value: 1\n      },\n      {\n        text: 'February',\n        value: 2\n      },\n      {\n        text: 'March',\n        value: 3\n      },\n      {\n        text: 'April',\n        value: 4\n      },\n      {\n        text: 'May',\n        value: 5\n      },\n      {\n        text: 'June',\n        value: 6\n      },\n      {\n        text: 'July',\n        value: 7\n      },\n      {\n        text: 'August',\n        value: 8\n      },\n      {\n        text: 'September',\n        value: 9\n      },\n      {\n        text: 'October',\n        value: 10\n      },\n      {\n        text: 'November',\n        value: 11\n      },\n      {\n        text: 'December',\n        value: 12\n      }\n    ]\n\n    const objectData = [\n      {\n        text: 'Object 1',\n        value: {\n          object: 1\n        }\n      },\n      {\n        text: 'Object 2',\n        value: {\n          object: 2\n        }\n      },\n      {\n        text: 'Object 3',\n        value: {\n          object: 3\n        }\n      }\n    ]\n\n    return {\n      data,\n      objectData,\n      groupData: [\n        {\n          type: 'optgroup',\n          label: 'Group 1',\n          children: [\n            {\n              text: 'January',\n              value: 1\n            }\n          ]\n        },\n        {\n          type: 'optgroup',\n          label: 'Group 2',\n          children: [\n            {\n              text: 'July',\n              value: 7\n            }\n          ]\n        }\n      ],\n      model1: [1],\n      model2: data[0],\n      model3: objectData[0].value,\n      group: [1, 7]\n    }\n  },\n  methods: {\n    change1 () {\n      console.log(typeof this.model1, this.model1)\n    },\n    change2 () {\n      console.log(typeof this.model2, this.model2)\n    },\n    change3 () {\n      console.log(typeof this.model3, this.model3)\n    }\n  }\n}\n</script>\n\n<style scoped>\nselect {\n  width: 100%;\n}\n</style>\n"