export default "<template>\n  <div>\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Single Select\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect>\n          <option value=\"1\">\n            January\n          </option>\n          <option value=\"2\">\n            February\n          </option>\n          <option value=\"3\">\n            March\n          </option>\n          <option value=\"4\">\n            April\n          </option>\n          <option data-divider=\"true\" />\n          <option value=\"5\">\n            May\n          </option>\n          <option value=\"6\">\n            June\n          </option>\n          <option value=\"7\">\n            July\n          </option>\n          <option value=\"8\">\n            August\n          </option>\n          <option data-divider=\"true\" />\n          <option value=\"9\">\n            September\n          </option>\n          <option value=\"10\">\n            October\n          </option>\n          <option value=\"11\">\n            November\n          </option>\n          <option value=\"12\">\n            December\n          </option>\n        </MultipleSelect>\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Multiple Select\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect multiple=\"multiple\">\n          <option value=\"1\">\n            January\n          </option>\n          <option value=\"2\">\n            February\n          </option>\n          <option value=\"3\">\n            March\n          </option>\n          <option value=\"4\">\n            April\n          </option>\n          <option data-divider=\"true\" />\n          <option value=\"5\">\n            May\n          </option>\n          <option value=\"6\">\n            June\n          </option>\n          <option value=\"7\">\n            July\n          </option>\n          <option value=\"8\">\n            August\n          </option>\n          <option data-divider=\"true\" />\n          <option value=\"9\">\n            September\n          </option>\n          <option value=\"10\">\n            October\n          </option>\n          <option value=\"11\">\n            November\n          </option>\n          <option value=\"12\">\n            December\n          </option>\n        </MultipleSelect>\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Group Select\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect multiple=\"multiple\">\n          <option data-divider=\"true\" />\n          <optgroup label=\"Group 1\">\n            <option value=\"1\">\n              January\n            </option>\n            <option value=\"2\">\n              February\n            </option>\n            <option value=\"3\">\n              March\n            </option>\n            <option data-divider=\"true\" />\n            <option value=\"4\">\n              April\n            </option>\n            <option value=\"5\">\n              May\n            </option>\n            <option value=\"6\">\n              June\n            </option>\n          </optgroup>\n          <option data-divider=\"true\" />\n          <optgroup label=\"Group 2\">\n            <option value=\"7\">\n              July\n            </option>\n            <option value=\"8\">\n              August\n            </option>\n            <option value=\"9\">\n              September\n            </option>\n            <option data-divider=\"true\" />\n            <option value=\"10\">\n              October\n            </option>\n            <option value=\"11\">\n              November\n            </option>\n            <option value=\"12\">\n              December\n            </option>\n          </optgroup>\n        </MultipleSelect>\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Data Select 1\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect :data=\"data\" />\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Data Select 2\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect\n          multiple=\"multiple\"\n          :data=\"data\"\n        />\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      data: [\n        {\n          value: 1,\n          text: 'Option 1'\n        },\n        {\n          value: 2,\n          text: 'Option 2'\n        },\n        {\n          value: 3,\n          text: 'Option 3'\n        },\n        {\n          divider: true\n        },\n        {\n          value: 4,\n          text: 'Option 4'\n        },\n        {\n          value: 5,\n          text: 'Option 5'\n        },\n        {\n          value: 6,\n          text: 'Option 6'\n        }\n      ]\n    }\n  }\n}\n</script>\n\n<style scoped>\nselect {\n  width: 100%;\n}\n</style>\n"